$color-bg-white: #fff;
$color-text-dark: #060b35;
$color-text-gray: #616070;
$main-color: #00acdc;

.page {
  font-family: Roboto, Aria, sans-serif;

  &__body {
    margin: 0;
  }
}

.card {
  box-sizing: border-box;
  width: 200px;
  height: 408px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: $color-bg-white;
  padding: 32px 16px 16px;

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }
  
  &__title {
    margin: 0 0 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    color: #060b35;
  }
  
  &__code {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    text-align: left;
    color: $color-text-gray;
    margin-bottom: 16px;
  }
  
  &__review-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__review-count {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }
  
  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 16px;
  }
  
  &__price-lebel {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: $color-text-gray;
  }
  
  &__price {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }
  
  &__buy {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 166px;
    height: 40px;
    border-radius: 5px;
    background-color: $main-color;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    color: $color-bg-white;
    text-transform: uppercase;
  
    &:hover {
      background-color: $color-bg-white;
      color: $main-color;
      border: 1px solid $main-color;
    }
  }
}


.stars {
  display: flex;
}

.stars__star {
  background-image: url(../images/star.svg);
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-repeat: no-repeat;
  background-position: center;
}

.stars:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n + 1),
.stars--2 .stars__star:nth-child(-n + 2),
.stars--3 .stars__star:nth-child(-n + 3),
.stars--4 .stars__star:nth-child(-n + 4),
.stars--5 .stars__star:nth-child(-n + 5) {
  background-image: url(../images/star-active.svg);
}


